/* grid */
.row, .col {
  position: relative;
  width: 100%;
  display: flex;
}

@media screen and (max-width: 1200px) {
  .row {
    flex-direction: column;
  }
}

.col {
  flex-direction: column;
}

.box {
  position: relative;
  display: flex;
  flex: 1;
  flex-direction: column;
  margin: 1%;
  justify-content: space-between;
}

.fill {
  flex: 1;
}

.center {
  justify-content: space-between;
  align-items: center;
}

.wrapper {
  max-width: desktop;
  margin: 0 auto;
}

.fixed_width {
  max-width: 1200px;
  padding: 0;
}

.one_quarter {
  flex: 0 0 23%;
}

.half {
  flex: 0 0 48%;
}

.three_quarter {
  flex: 0 0 74.92308%;
}

.one_third {
  flex: 0 0 32%;
}

@media screen and (min-width: 1200px) {
  .one_third:first-of-type {
    margin-left: 0;
  }
  .one_third:nth-of-type(3n+3) {
    margin-right: 0;
  }
}

.icon {
  font-size: 10px;
}

.icon:before {
  content: ' ';
  vertical-align: middle;
  display: inline-block;
  background-image: url("/images/sprite.svg");
  background-repeat: no-repeat;
  background-size: 1.6em 4.8em;
}

.icon.arrow_up:before {
  background-position: 0em 0em;
  width: 1.6em;
  height: 1.6em;
}

.icon.close:before {
  background-position: 0em -1.6em;
  width: 1.6em;
  height: 1.6em;
}

.icon.menu_lines:before {
  background-position: 0em -3.2em;
  width: 1.6em;
  height: 1.6em;
}

nav a {
  text-decoration: none;
}

/* nav#primary */
nav#primary {
  position: relative;
  transition: transform 0.3s;
  will-change: transform;
  text-transform: uppercase;
  font-size: 12px;
  margin-top: 12px;
}

@media screen and (max-width: 1000px) {
  nav#primary > ul {
    position: fixed;
    width: 100%;
    transform: translateX(100vw);
    flex-direction: column;
    font-size: 1.4em;
  }
  nav#primary > ul .submenu {
    margin-bottom: 1em;
    margin-left: 1em;
    border-left: 2px solid #000;
  }
}

nav#primary ul {
  background: #fff;
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  justify-content: space-between;
  text-align: left;
  z-index: 1;
}

nav#primary ul li {
  position: relative;
}

nav#primary ul li .submenu {
  flex-direction: column;
}

nav#primary ul li:hover .submenu {
  left: -60px;
}

.current nav#primary ul li a {
  font-weight: 900;
  text-decoration: underline;
}

nav#primary ul a {
  display: block;
  padding: 0.5em 1em;
}

@media screen and (min-width: 1000px) {
  nav#primary .submenu {
    position: absolute;
    left: -9999em;
    width: 200px;
    background: #fff;
  }
}

#menutoggle, #scroll {
  display: none;
  cursor: pointer;
}

#menutoggle {
  position: absolute;
  right: 1em;
  top: 1em;
}

@media screen and (max-width: 1000px) {
  #menutoggle {
    display: block;
  }
}

#scroll {
  position: fixed;
  right: 1em;
  top: 40vh;
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /* IE10+ CSS styles go here */
}

* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  min-height: 100vh;
  font-family: "Nunito Sans", sans-serif;
}

@media screen and (max-width: 1200px) {
  body {
    padding: 0 0.5em;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1rem;
  text-rendering: optimize-legibility;
}

#content {
  transition: transform 0.3s;
  will-change: transform;
  flex: 1 0 auto;
  width: 100%;
}

#content h2 {
  font-size: 1.4rem;
}

#content h3 {
  font-size: 1.2rem;
}

@media screen and (max-width: 1200px) {
  #content {
    margin: 0 1em;
  }
}

#content::after {
  content: '\00a0';
  display: block;
  margin-top: var(--space);
  height: 0px;
  visibility: hidden;
}

@media (--break-lg) {
  #content {
    padding-top: var(--space-lg);
  }
  #content::after {
    margin-top: var(--space-lg);
  }
}

#content:after {
  content: none;
}

#main,
#sidebar {
  text-align: left;
}

#main h1,
#main h2,
#main h3,
#main h4,
#sidebar h1,
#sidebar h2,
#sidebar h3,
#sidebar h4 {
  margin-top: 1em;
}

header,
footer {
  position: relative;
}

header {
  z-index: 100;
}

header h1.logo {
  text-indent: -9999em;
  padding: 0;
  width: 200px;
  height: 120px;
  margin: 0;
  background: center center url(/images/logo.png) no-repeat;
  background-size: 100%;
  margin-right: 24px;
}

@media screen and (max-width: 1200px) {
  header {
    text-align: left;
  }
}

header .btn {
  margin: 0 12px;
}

footer nav {
  display: inline-block;
}

a {
  color: #444;
}

a:hover {
  color: #2b2b2b;
}

.reveal {
  transform: translateX(-100vw);
}

#banner {
  position: relative;
  width: 100%;
  height: 450px;
  overflow: hidden;
}

#banner .banner_content, #banner .banner_shape, #banner .banner_bg {
  position: absolute;
  width: 100%;
}

#banner .banner_content {
  z-index: 30;
  color: #fff;
  margin: 3em;
}

#banner .banner_content h2.tagline {
  font-weight: 100;
  font-size: 44px;
  margin-bottom: 0;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

#banner .banner_content h3.subtag {
  font-weight: 100;
  font-size: 22px;
}

#banner .banner_content .btn {
  font-size: 24px;
  margin: auto;
  display: inline-block;
  margin-left: 300px;
  margin-top: 40px;
}

#banner .banner_shape {
  z-index: 20;
  width: 2000px;
  height: 1200px;
  background: #000;
  transform: translateX(-1000px) rotate(-65deg);
}

#banner .banner_bg img {
  width: 100%;
  height: auto;
}

.full_content {
  padding: 0 1em;
}

.btn {
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
  font: "Nunito Sans", sans-serif;
  padding: .5em 1em;
  color: #e4290a;
  border: 2px solid #e4290a;
  text-decoration: none;
  cursor: pointer;
  border-radius: .3em;
  transition: all .3s ease;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  font-family: inherit;
  letter-spacing: .1em;
}

.btn:hover {
  background: #e4290a;
  transition: background 0.3s ease;
  opacity: 1;
  color: #fff;
}

.btn:active {
  background: #6a1305;
}

.btn .fa {
  transition: transform .2s ease;
}

.callout {
  text-align: left;
  border: 1px solid;
  padding: .5em;
}

.callout h3 {
  text-align: center;
}

.tel {
  text-align: center;
  font-size: 26px;
  text-decoration: none;
  font-weight: 900;
}

.login {
  text-transform: uppercase;
  font-weight: 900;
  text-decoration: none;
  font-size: 13px;
}

.home h2 {
  text-transform: uppercase;
  font-weight: 300;
}
